const DefaultNode = ({ data }: DefaultNodeProps) => {
  const [prompt, setPrompt] = useState<string>('')
  const [disabled, setDis] = useState<boolean>(data.prompt === '')
  const [value, setValue] = React.useState(0);

  const handleChange = (event: React.SyntheticEvent, newValue: number) => {
    setValue(newValue);
  };

  const sendPrompt = () => {
    setValue(1)
    setDis(false)
    data.prompt = prompt
    data.content = defaultContent
    data.selected = defaultSelected
    data.suggestions = [
      {
        id: '1',
        content: defaultSugContent
      },
      {
        id: '2',
        content: defaultSugContent
      },
      {
        id: '3',
        content: defaultSugContent
      },
    ]
  }

  const input = (<div>
    <textarea
      className='node-input-area'
      value={prompt}
      onChange={(e) => setPrompt(e.target.value)}
      rows={5}/>
    <Button 
      onClick={sendPrompt}
      variant="contained">Send</Button>
    
  </div>
  )

  return (
    <div className='default-node'>
      <Box sx={{ borderBottom: 1, borderColor: 'divider' }}>
        <Tabs value={value} onChange={handleChange} aria-label="basic tabs example">
          <Tab label="Prompt" {...a11yProps(0)} />
          <Tab disabled={disabled} label="Content" {...a11yProps(1)} />
        </Tabs>
      </Box>
      <CustomTabPanel value={value} index={0}>
        {disabled ? input: data.prompt}
      </CustomTabPanel>
      <CustomTabPanel 
        value={value} 
        index={1}>
        {data.content}
      </CustomTabPanel>
      <Handle
        type="source"
        position={Position.Right}
        id="a"
        style={{ top: 'calc(50% - 25px)', background: '#555', width: '10px', height: '50px', borderRadius: '0' }}
      />
      <Handle
        type="target"
        position={Position.Left}
        id="b"
        style={{ bottom: 10, top: 'auto', background: '#555' }}
      />
    </div>
  );
};

type Node = {
  origin: number[],
  id:string,
  type:string,
  position: {
      x: number,
      y: number
  },
  data: {
      id:string,
      title:string,
      content:string,
      prompt:string,
      selected:string,
      suggestions: Suggestion[]
  }
}

const initialEdges: Edge[] = [];
const initialNodes: Node[] = [
  {
    id: "0",
    type: "defaultNode",
    position: {
        x: 139,
        y: 172
    },
    data: {
        id: "1",
        title: "",
        content: "",
        prompt: "",
        selected: "",
        suggestions: []
    },
    origin: [0.5, 0]
},
];
const nodeTypes = {
  defaultNode: DefaultNode
};

export default function Project() {
  const { id } = useParams();
  const [nodes, setNodes, onNodesChange] = useNodesState(initialNodes);
  const [edges, setEdges, onEdgesChange] = useEdgesState(initialEdges);
  const [connectingEdge, setConnectingEdge] = useState({nodeId: '', id: '', handleId: ''});

  const onConnect = useCallback(
    (params: Edge | Connection) => setEdges((eds) => addEdge(params, eds)),
    [setEdges],
  );
 
  const onConnectStart = useCallback((_: any, edge: any) => {
    console.log(edge)
    setConnectingEdge(edge)
  }, []);

  const onConnectEnd = useCallback(
    (event: any) => {
      const targetIsPane = event.target.classList.contains('react-flow__pane');

      if (targetIsPane) {
        const id = getId().toString();
        const newNode: Node = {
          id,
          type: "defaultNode",
          position: {
            x: event.clientX,
            y: event.clientY,
          },
          data: { 
            id,
            title: "",
            content: "",
            prompt: "",
            selected: "",
            suggestions: []
          },
          origin: [0.5, 0.0],
        };
        setNodes((nds) => nds.concat(newNode));
        setEdges((eds) => [...eds, { id, source: connectingEdge.nodeId, target: id, sourceHandle: connectingEdge.handleId, targetHandle: 'a' }]);
      }

      console.log(nodes)
      console.log(edges)
    }, [connectingEdge],
  );

  return (
    <div style={{ width: '100vw', height: '100vh', overflow: "hidden" }}>
      <ReactFlow
        nodes={nodes}
        edges={edges}
        nodeTypes={nodeTypes}
        onNodesChange={onNodesChange}
        onEdgesChange={onEdgesChange}
        onConnect={onConnect}
        onConnectStart={onConnectStart}
        onConnectEnd={onConnectEnd}
        fitView
        proOptions={{hideAttribution: true}}
      >
        <MiniMap/>
      <Controls/>
      </ReactFlow>
    </div>
  );
}
