import React from 'react'
import { AppBar, Toolbar, Link, Grid } from '@mui/material';
import { useNavigate } from 'react-router-dom';


export default function Header() {
    const navigate = useNavigate();

    
    return (
        <header>
        <AppBar position="relative">
          <Toolbar>
            <Grid container>
              <Grid item xs={2}>
                  <Link 
                    onClick={(e) => {
                      e.preventDefault()
                      navigate('/')
                    }}
                    href="/" 
                    color="inherit">Home</Link>
              </Grid>
              
              <Grid item xs={2}>
                  <Link 
                    onClick={(e) => {
                      e.preventDefault()
                      navigate('/config')
                    }}
                    href="/config" 
                    color="inherit">Config</Link>
              </Grid>
              
              <Grid item xs={2}>
                  <Link 
                    onClick={(e) => {
                      e.preventDefault()
                      navigate('/login')
                    }}
                    href="/login" 
                    color="inherit">Login</Link>
              </Grid>
              
            </Grid>

            
          </Toolbar>
        </AppBar>
      </header>
    )
}
