import { Button, Card, CardActions, CardContent, Container, Grid, Link, Stack, Typography } from '@mui/material'
import React, { useState } from 'react'
import { useNavigate } from 'react-router-dom';


type ProjectCardPropType = {
  id: number
}
function ProjectCard({id}: ProjectCardPropType) {
  const navigate = useNavigate();
  const href = `/project/${id}`
  return (
    <Card variant="outlined">

    <React.Fragment>
      <CardContent>
        <Typography sx={{ fontSize: 14 }} color="text.secondary" gutterBottom>
          Word of the Day
        </Typography>
        <Typography variant="h5" component="div">
          Lorem ipsum dolor sit amet.
        </Typography>
        <Typography sx={{ mb: 1.5 }} color="text.secondary">
          adjective
        </Typography>
        <Typography variant="body2">
          well meaning and kindly.
          <br />
          {'"a benevolent smile"'}
        </Typography>
      </CardContent>
      <CardActions>
        <Link 
          onClick={(e) => {
            e.preventDefault()
            navigate(href)
          }}
          href={href}
          color="inherit">Open</Link>
      </CardActions>
    </React.Fragment>

    </Card>
    
  )
}


export default function Home() {
  const [personalProjects, setPersonalProjects] = useState([1,2,3,4,5,6,7])
  const [teamProjects, setTeamProjects] = useState([1,2,3,4,5])

  return (
    <>
        <Container>
            <Typography px={3} align="center" variant="h4">
                Personal projects <Button 
                  onClick={() => setPersonalProjects(prs => [prs[prs.length-1]+1, ...prs])} 
                  variant='contained'>Create Project</Button>
            </Typography>
            <Stack spacing={{ xs: 1, sm: 2 }} direction="row" useFlexGap flexWrap="wrap">
              {personalProjects.map(id => <ProjectCard key={id.toString()} id={id}/>)}              
            </Stack>
        </Container>

        <Container>
            <Typography align="center" variant="h4">
                Team projects
            </Typography>

            <Stack spacing={{ xs: 1, sm: 2 }} direction="row" useFlexGap flexWrap="wrap">
              <ProjectCard id={1}/>
              <ProjectCard id={1}/>
              <ProjectCard id={1}/>
              <ProjectCard id={1}/>
              <ProjectCard id={1}/>
            </Stack>
        </Container>
    </>
  )
}
